import { Capacitor } from '@capacitor/core'
import { File } from "@ionic-native/file"
import { FirebaseAuthentication } from '@ionic-native/firebase-authentication'
import { OneSignal as OneSignalCapacitor } from '@ionic-native/onesignal'
import { OpenNativeSettings } from '@ionic-native/open-native-settings'
import { IonToggle } from '@ionic/react'
import { runInAction } from 'mobx'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { useHistory } from 'react-router'
import { AdminItemFieldRegistro, Page } from '../../components'
import { ModalInfo } from '../../components/ModalInfo'
import { Link as LinkSub, SubHeader } from '../../components/SubHeader'
import { RUTAS, RUTAS_APP_FABRICANTE } from '../../constants'
import { format_numero_telefono_verificar, useApiToaster, useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import './FabricanteConfiguracion.css'

const defaultValues = {
  id: 0,
  contrasena: '',
  repetir_contrasena: '',
  contrasena_actual: '',
}

export const FabricanteConfiguracion: React.FC = observer(() => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const history = useHistory()
  const apiToaster = useApiToaster()
  const [FaltaDocumento, setFaltaDocumento] = useState(false)
  const [ModalCambioContrasena, setModalCambioContrasena] = useState(false)
  const [Contrasena, setContrasena] = useState<null | string>()
  const [Datos, setDatos] = useState(defaultValues)
  const [ContrasenaRepetir, setContrasenaRepetir] = useState<null | string>('')
  const [Loading, setLoading] = useState(false)
  const [Errors, setErrors] = useState(defaultValues)
  const [DropSonido, setDropSonido] = useState(false)
  const [CiudadesSeleccionadas, setCiudadesSeleccionadas] = useState([] as any)
  const [DropNotificaciones, setDropNotificaciones] = useState(false)
  const [Links, setLinks] = useState<LinkSub[]>()
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)

  const INDEXES = {
    opciones: 0,
  }

  const [Index, setIndex] = useState(INDEXES.opciones)
  const addToast = useToaster()
  const setup = () => {
    if (Capacitor.getPlatform() !== 'web') {
      FirebaseAuthentication.signOut().catch((reason) =>
        console.log('error en FirebaseAuthentication.signOut:' + reason),
      )
    }
    if (loginStore.fabricante) {
      let datos = { ...loginStore.fabricante }
    }
  }

  React.useEffect(() => {
    if (Index === INDEXES.opciones) {
      setLinks([{ titulo: 'Perfil', ruta: RUTAS_APP_FABRICANTE.PERFIL }, { titulo: 'Configuracin' }])
    }
  }, [Index])

  React.useEffect(() => {
    // controlamos el estado del dispositivo
    if (Capacitor.getPlatform() !== 'web') {
      OneSignalCapacitor.getPermissionSubscriptionState().then(activo => {
        if (!activo.subscriptionStatus.subscribed) {
          loginStore.setNotificacionesActivas(activo.subscriptionStatus.subscribed)
        }
      })
    }
  }, [])

  const activar_desactivar = (activo: boolean, tipo: number) => {
    if (tipo === 10) {
      OneSignalCapacitor.enableSound(activo)
      const success_msg = activo ? 'Notificaciones activadas' : 'Notificaciones desactivadas'
      const error_msg = activo ? 'activar las notificaciones' : 'desactivar las notificaciones'
      if (activo) {
        if (Capacitor.getPlatform() !== 'web') {
          OneSignalCapacitor.getIds().then((ids) => {
            loginStore.setOneSignal(ids.userId, ids.pushToken)
            console.log("@ids",ids.userId, ids.pushToken)

            loginStore.environment.api.setDevice(ids.userId, ids.pushToken, activo).then((result) => {
              console.log('setnotificaciones',result)
              apiToaster(result, success_msg, error_msg)
              if (result.kind === 'ok') {
                loginStore.setNotificacionesActivas(activo)
              }
            })
          })
        }
      }
    } else {
      loginStore.setSonidosActivos(activo)
      let success_msg
      // se crea archivo para saber si va a apagar el sonido o no
      if (activo) {
        addToast('Sonidos activados', 'success')
        //borra archivo si existe
        File.checkFile(File.dataDirectory, 'no_sound').then(doesExist => {
          if (doesExist) {
            File.removeFile(File.dataDirectory, 'no_sound')
          }
        }).catch(e => {
          console.log('sonido activo')
        })
      } else {
        addToast('Sonidos desactivadoss', 'success')
        //creamos el archivo si no existe
        File.checkFile(File.dataDirectory, 'no_sound').then(doesExist => {
          console.log('sonido desactivado')
        }).catch(e => {
          console.log('sonido desactivado')
          File.createFile(File.dataDirectory, 'no_sound', false).then(resultado => {
            console.log(resultado)
          })
        })
      }
    }
  }

  const setDato = (campo: any, valor: string) => {
    const datos = { ...Datos }
    //@ts-ignore
    datos[campo] = valor
    setDatos(datos)
  }

  const validar_campos = () => {
    setErrors(defaultValues)
    let errors = { ...Errors }
    let error = false

    if (Datos.contrasena_actual === '') {
      errors['contrasena_actual'] = 'Se requiere la contrasea actual'
      error = true
    }

    if (Datos.contrasena && Datos.contrasena.trim() === '') {
      errors['contrasena'] = 'Se requiere una contrasea'
      error = true
    }

    if (Datos.contrasena && Datos.repetir_contrasena && Datos.contrasena !== Datos.repetir_contrasena) {
      errors['contrasena'] = 'Las contraseas no coinciden'
      error = true
    }
    setErrors(errors)
    return !error
  }

  const guardar = (datos_guardar?: any) => {
    setLoading(true)
    setErrors(defaultValues)
    const datos = !!datos_guardar ? { ...datos_guardar } : { ...Datos }
    const validacion = validar_campos()
    datos.nro_telefono = format_numero_telefono_verificar(datos.nro_telefono)
    datos['username'] = loginStore.username
    datos['id'] = loginStore.id
    if (validacion) {
      rootStore.environment.api.guardarCliente(datos).then((result: any) => {
        apiToaster(result, 'Datos actualizados correctamente', 'guardar')
        setLoading(false)
        if (result.kind === 'ok') {
          runInAction(() => {
            loginStore.setUser(result.user)
            loginStore.setApiToken(result.user.token)
          })
          // obtenerDatosCliente()
          history.push(RUTAS_APP_FABRICANTE.CONFIGURACION)
          setModalCambioContrasena(false)
        } else if (result.kind === 'bad-data') {
          setErrors(result.errors)
        }
      })
    } else {
      setLoading(false)
    }
  }

  return (
    <Page login_required fabricante={true} disableScroll={true} id={'menu-perfil-fabricante'}>
      <ModalInfo
        isOpen={ModalCambioContrasena}
        close={() => setModalCambioContrasena(false)}
        titulo="Cambio de contrasea"
        color="var(--azul-fabricante)"
        cssClass="modal-cambiar-contrasena"
      >
        <>
          <div style={{}}>
            <AdminItemFieldRegistro
              deshabilitado={!Loading}
              titulo={'Contrasea actual'}
              tipo={'password'}
              campo={'contrasena_actual'}
              valor={Datos.contrasena_actual ? Datos.contrasena_actual : ''}
              error={Errors && Errors.contrasena_actual}
              set_dato={setDato}
              columnas={12}
              columnas_sm={12}
            />
          </div>
          <div style={{}}>
            <AdminItemFieldRegistro
              deshabilitado={!Loading}
              titulo={'Contrasea'}
              tipo={'password'}
              campo={'contrasena'}
              valor={Datos.contrasena ? Datos.contrasena : ''}
              error={Errors && Errors.contrasena}
              set_dato={setDato}
              columnas={12}
              columnas_sm={12}
            />
          </div>
          <div style={{}}>
            <AdminItemFieldRegistro
              deshabilitado={!Loading}
              titulo={'Repetir contrasea'}
              tipo={'password'}
              campo={'repetir_contrasena'}
              valor={Datos.repetir_contrasena ? Datos.repetir_contrasena : ''}
              error={Errors && Errors.repetir_contrasena}
              set_dato={setDato}
              columnas={12}
              columnas_sm={12}
            />
          </div>
          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr 1fr' }}>
            <div />
            <button
              className="btn btn-primary"
              title={'Guardar contrasea'}
              onClick={() => {
                guardar()
              }}
            >
              Guardar
            </button>
          </div>
        </>
      </ModalInfo>
      <SubHeader noContainer links={[{ titulo: 'Configuracin' }]} />

      <div className="container-menu-perfil ml-3 mt-5">
        <h4 style={{ color: 'var(--gris-oscuro-abi)', padding: 0 }}>Configuracin</h4>
        <a className="" onClick={() => history.push(RUTAS_APP_FABRICANTE.API)}>
          <img src={'assets/icon/api.png'} />
          <span> API</span>
        </a>
        <a className="mb-2" onClick={() => history.push(RUTAS_APP_FABRICANTE.DOCUMENTOS)}>
          <img src={'assets/icon/credit-card.png'} />

          <span> Documentos</span>
          {FaltaDocumento && <FIcon.AlertCircle size={20} style={{ marginLeft: 5, color: 'var(--azul-fabricante)' }} />}
        </a>

        <a className="mb-2" onClick={() => history.push(RUTAS_APP_FABRICANTE.HORARIO_ATENCION)}>
          <img src={'assets/icon/Factura.png'} />

          <span>Horario de atencin</span>
        </a>
        {Movil && (
          <a
            className="mb-2"
            onClick={() => {
              setDropNotificaciones(!DropNotificaciones)
            }}
          >
            <img src={'assets/icon/notificacion.png'} />
            <span> Notificaciones</span>
            {!DropNotificaciones && <FIcon.ChevronRight size={18} />}
            {DropNotificaciones && <FIcon.ChevronDown size={18} />}
          </a>
        )}
        {DropNotificaciones && (
          <div>
            <div />
            <IonToggle
              onIonChange={(e) => activar_desactivar(!loginStore.notificacionesActivas, 10)}
              className="toggle-registro-abi"
              checked={loginStore.notificacionesActivas}
            />
          </div>
        )}
        {Movil && (
          <a
            className="mb-2"
            onClick={() => {
              setDropSonido(!DropSonido)
            }}
          >
            <img src={'assets/icon/sonido.png'} />
            <span>Sonido</span>
            {!DropSonido && <FIcon.ChevronRight size={18} />}
            {DropSonido && <FIcon.ChevronDown size={18} />}
          </a>
        )}
        {DropSonido && (
          <div>
            <div />
            <IonToggle
              onIonChange={(e) => activar_desactivar(!loginStore.sonidosActivos, 20)}
              className="toggle-registro-abi"
              checked={loginStore.sonidosActivos}
            />
          </div>
        )}
        {Movil && (
          <a className="mb-2" onClick={() => OpenNativeSettings.open('application_details')}>
            <img src={'assets/icon/documentos.png'} />
            <span>Permisos de ABi</span>
          </a>
        )}
        <a className="mb-2" onClick={() => setModalCambioContrasena(true)}>
          <img src={'assets/icon/candado.png'} />
          <span>Cambiar contrasea</span>
        </a>
        <a className="mb-2" onClick={() => history.push(RUTAS_APP_FABRICANTE.CAMBIO_MODO)}>
          <img src={'assets/icon/cambiar-cuenta.png'} />
          <span>Cambiar cuenta</span>
        </a>
        <a className="mb-2" onClick={() => history.push(RUTAS.LOGOUT)} style={{ color: 'var(--naranja-abi)' }}>
          <img src={'assets/icon/cerrar-sesion.png'} />
          <span>Cerrar Sesin</span>
        </a>
      </div>
    </Page>
  )
})

export default FabricanteConfiguracion
